<template>
  <UPage>
    <UPageHeader title="Sitemap" description="All pages on our website" />
    
    <UPageBody>
      <div class="space-y-8">
        <section>
          <h2 class="text-xl font-semibold mb-4">Main Pages</h2>
          <ul class="space-y-2">
            <li><NuxtLink to="/" class="text-primary hover:underline">Home</NuxtLink></li>
            <li><NuxtLink to="/about" class="text-primary hover:underline">About</NuxtLink></li>
            <li><NuxtLink to="/shop" class="text-primary hover:underline">Shop</NuxtLink></li>
            <li><NuxtLink to="/legal" class="text-primary hover:underline">Legal</NuxtLink></li>
          </ul>
        </section>

        <section v-if="shopPages?.length">
          <h2 class="text-xl font-semibold mb-4">Shop Pages</h2>
          <ul class="space-y-2">
            <li v-for="page in shopPages" :key="page._path">
              <NuxtLink :to="page._path" class="text-primary hover:underline">
                {{ page.title }}
              </NuxtLink>
            </li>
          </ul>
        </section>

        <section v-if="legalPages?.length">
          <h2 class="text-xl font-semibold mb-4">Legal Pages</h2>
          <ul class="space-y-2">
            <li v-for="page in legalPages" :key="page._path">
              <NuxtLink :to="page._path" class="text-primary hover:underline">
                {{ page.title }}
              </NuxtLink>
            </li>
          </ul>
        </section>

        <section v-if="categories?.length">
          <h2 class="text-xl font-semibold mb-4">Categories</h2>
          <ul class="space-y-2">
            <li v-for="category in categories" :key="category">
              <NuxtLink :to="`/category/${category.toLowerCase().replace(/\s+/g, '-')}`" class="text-primary hover:underline">
                {{ category }}
              </NuxtLink>
            </li>
          </ul>
        </section>
      </div>
    </UPageBody>
  </UPage>
</template>

<script setup lang="ts">
const { data: shopPages } = await useAsyncData('sitemap-shop', () =>
  queryCollection('shop').all()
)

const { data: legalPages } = await useAsyncData('sitemap-legal', () =>
  queryCollection('legal').all()
)

const categories = computed(() => {
  const cats = new Set<string>()
  shopPages.value?.forEach(page => {
    if (page.category) cats.add(page.category)
  })
  return Array.from(cats)
})
</script>